.container1{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: linear-gradient(140deg,rgb(36,36,223),rgb(39,39,166));
}

.calculator{
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}

form input{
  width: 60px;
  height: 60px;
  font-size: 16px;
  border: none;
  outline: 0;
  background-color: rgb(91, 91, 151);
  margin: 2px;
  border-radius: 10px;
  color: white;
  font-weight: bolder;
  cursor: pointer;
}

form input[type="button"]:hover{
  background-color: blueviolet;
}

form .display{
  display: flex;
  justify-content: flex-end;
  margin: 5px 0px 15px 0px;

}

form .display input{
  text-align: right;
  flex: 1;
  font-size: 40px;
  padding: 5px 10px ;
  background-color: rgb(64, 64, 64);
}
form input.equal{
  width: 123px ;
}

